import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  AfterViewInit
} from '@angular/core';
import { CurrencyPipe, DatePipe } from '@angular/common';

@Component({
  selector: 'nx-table-row-detail-a',
  template: require('./table-row-detail-a.component.html')
})
export class NxTableRowDetailAComponent implements OnInit, AfterViewInit {
  @Input()
  public content: any;

  @Input()
  public config: any;

  @Output()
  public clickEvent: EventEmitter<any> = new EventEmitter<any>();

  public detailKeys: any = [];

  constructor() {}

  ngOnInit() {
    // console.log(this.content);
  }

  ngAfterViewInit() {
    if (this.content.detail) {
      this.config.detailColumns.forEach((key: any) => {
        this.detailKeys.push(key);
      });
    }
  }

  public parseData(key: any) {
    let newVal = this.content.detail[key.column];
    switch (key.pipe) {
      case 'currency':
        newVal = newVal ? new CurrencyPipe('en-US').transform(newVal) : '';
        break;
      case 'date':
        newVal = newVal ? new DatePipe('en-US').transform(newVal, key.format) : '';
        break;
      default:
        newVal = newVal ? newVal.toString() : '';
        break;
    }

    return newVal.toString();
  }

  public showClick(_event: any) {
    this.clickEvent.emit(_event);
  }
}



