import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { AppService } from '../../services';
import { Router } from '@angular/router';

@Component({
  selector: 'nx-finder',
  styles: [require('./finder.component.scss')],
  template: require('./finder.component.html')
})
export class FinderComponent implements OnInit {
  @Input()
  public collection: any[] = [];

  @Input()
  public headers: any[];

  @Input()
  public active: any;

  @Input()
  public route: string;

  @Output()
  public keyUp: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public findClick: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public afterClick: EventEmitter<any> = new EventEmitter<Object>();

  @Output()
  public newClick: EventEmitter<any> = new EventEmitter<Object>();

  @Input()
  public value: string;

  @Input()
  public config: any = {};

  @Input()
  public nxId = 'nx-finder';

  /**
   * Component constructor with injected dependencies.
   * @param appService
   */
  public constructor(private router: Router) {}

  /**
   * Implements onInit event handler.
   */
  ngOnInit() {}

  public keyupEvent(e: any) {
    this.keyUp.emit(e.target.value);
  }

  public clickEvent(e: any) {
    this.afterClick.emit(e);
    // this.collection = [];
    // this.value = e[this.fieldsShow[0]];
  }

  public closeEvent() {
    this.active = false;
    this.router.navigate([this.route]);
  }

  public findEvent() {
    this.findClick.emit(this.value);
  }

  public clearEvent() {
    this.value = '';
    this.findClick.emit(this.value);
  }

  public newEvent() {
    this.newClick.emit('ok');
  }
}



