import { Component, SimpleChange, OnChanges, Input } from "@angular/core";
import { Chart } from "chart.js";

import { deepExtend } from "./helper";

@Component({
  selector: "nx-doughnut-chart",
  template: require('./doughnut-chart.component.html')
})
export class DoughnutChartComponent implements OnChanges {
  @Input() public settings = {};

  @Input()
  public nxId = 'nx-doughnut-chart';

  public chart: Chart;

  public defaultSettings: object = {
    type: "pie",
    data: {
      labels: [],
      datasets: []
    },
    options: {
      legend: {
        display: true
      },
      tooltips: {
        backgroundColor: "#fff",
        titleFontColor: "#000",
        bodyFontColor: "#3c3c3c",
        displayColors: false,
        borderColor: "#d8d8d8",
        borderWidth: 1
      }
    }
  };

  constructor() {}

  ngOnChanges(changes: { [propertyName: string]: SimpleChange }) {
    const params = this.prepareSettings();
    console.log(params);

    if (this.settings) {
      this.setChart(params);
    }
  }

  public setChart(settings: any) {
    this.chart = new Chart("doughnut-chart-canvas", settings);
  }

  private prepareSettings(): object {
    return deepExtend({}, this.defaultSettings, this.settings);
  }
}



