import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from '@angular/core';

@Component({
  selector: 'nx-login-step-one',
  template: require('./nx-login-step-one.component.html'),
  styles: [require('./nx-login-step-one.component.scss')]
})
export class NxLoginStepOneComponent implements OnInit {
  @ViewChild('userName') trgFocusEl: ElementRef;

  @Output() public helpEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output() public nextEvent: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  public inputSettings = {
    inputLabel: '',
    inputMinChars: 8,
    inputMaxChars: 10,
    browserVersionsRef: ''
  };

  @Input() public errorMsg = '';
  @Output() public errorMsgChange: EventEmitter<string> = new EventEmitter<
    string
  >();

  @Input() public errorBrowserMsg = '';
  @Output() public errorBrowserMsgChange: EventEmitter<
    string
  > = new EventEmitter<string>();

  @Input() public restrictionMessage = {
    header: "Seguimos mejorando",
    content : "¡Descarga ya! la App!.",
    imgButton: [{url:"#", img:"../../../../assets/img/appstore.png"}, {url:"#", img:"../../../../assets/img/appstore.png"}],
    footer: "Si deseas realizar ...",
  }

  @Input() public restrictionActive = true;

  @Input()
  public logoUrl = '/assets/img/logo.png';

  @Input()
  public contactInfo = [
    {place: 'Monterrey', phone: '2267 3446', description: '(BANREGIO)'},
    {place: 'Resto del país', phone: '01 81 2267 3446', description: '(BANREGIO)'},
    {place: 'EUA y Canadá', phone: '1 866 4320 550', description: ''}
  ];

  public name = '';
  public nameTmp = '';

  public disabledBtn = true;

  isHide = true;

  constructor() {}

  ngOnInit() {
    this.trgFocusEl.nativeElement.focus();
  }

  public checkBrowsers() {
    const sayswho = (function() {
      let ua = navigator.userAgent,
        tem,
        M =
          ua.match(
            /(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i
          ) || [];
      if (/trident/i.test(M[1])) {
        tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
        return 'IE ' + (tem[1] || '');
      }
      if (M[1] === 'Chrome') {
        tem = ua.match(/\b(OPR|Edge)\/(\d+)/);
        if (tem != null)
          return tem
            .slice(1)
            .join(' ')
            .replace('OPR', 'Opera');
      }
      M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
      if ((tem = ua.match(/version\/(\d+)/i)) != null) M.splice(1, 1, tem[1]);
      return M.join(' ');
    })();

    console.log(sayswho);
    if (
      sayswho.indexOf('Opera') > -1 ||
      sayswho.indexOf('IE ') > -1
    ) {
      this.errorBrowserMsg =
      'versiones soportadas.';
    } else {
      let _version = sayswho.length > 4 ? sayswho.split(' ') : ['', '70'];
      // console.log(+_version[1]);
      if (+_version[1] < 45 && _version[0] != 'Safari') {
        this.errorBrowserMsg =
          'versiones soportadas.';
      }
    }
    const browserData = sayswho.length > 4 ? sayswho.split(' ') : ['', '70']
    if(browserData[0] === 'Chrome' && parseInt(browserData[1])  <= 50){
      this.errorBrowserMsg = 'versiones soportadas';
    }
  }

  public helpClick(e: any) {
    this.helpEvent.emit(e);
  }

  public nextClick(e: any) {
    this.nextEvent.emit(this.name);
  }

  public keyupEvent(e: any) {
    this.errorMsg = '';
    this.errorMsgChange.emit(this.errorMsg);

    if (this.name.length >= this.inputSettings.inputMinChars) {
      this.disabledBtn = false;

      if (e.keyCode === 13 || e.code === 'Enter') {
        let msg = e;
        this.nextClick(msg);
        this.name = '';
      }
    } else {
      this.disabledBtn = true;
    }

    if (this.name.length > this.inputSettings.inputMaxChars) {
      this.name = this.name.substr(0, this.inputSettings.inputMaxChars);
    }
  }

  public onPaste(e: any) {
    this.name = e;
  }

  public showUser() {
    this.isHide = !this.isHide;
  }
}



