import { Component, OnInit, Input, Output, EventEmitter,
  OnChanges, SimpleChanges } from '@angular/core';
// import { SearchService } from '../../services/libraries.service';
import { Subject } from 'rxjs/Subject';

@Component({
  selector: 'nx-autocomplete',
  template: require('./autocomplete.component.html')
})
export class AutocompleteComponent {
  @Input()
  public collection: Object;
  @Input()
  public fieldsShow: string[];

  @Output()
  public keyUp: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public afterClick: EventEmitter<any> = new EventEmitter<Object>();

  @Input()
  public value: string;

  @Input()
  public nxId = 'nx-autocomplete';

  private arrowkeyLocation = 0;

  constructor() {
    /**
     * Constructor
     */
  }

  public keyupEvent(e: any) {
    switch (e.keyCode) {
      case 38: // this is the ascii of arrow up
               this.arrowkeyLocation--;
               break;
      case 40: // this is the ascii of arrow down
               this.arrowkeyLocation++;
               break;
    }

    if (e.keyCode === 38 || e.keyCode === 40) {
      this.selectItem(e.target, this.arrowkeyLocation);
    } else if (e.keyCode === 13) {
      const data = this.collection as any[];
      this.afterClick.emit(data[this.arrowkeyLocation]);
      this.value = data[this.arrowkeyLocation][this.fieldsShow[0]];
      this.collection = [];
    } else {
      this.keyUp.emit(e.target.value);
    }
  }

  private ngOnChanges(changes: SimpleChanges) {
    if (changes['collection']) {
      this.collection = this.collection;
    }
  }

  private clickEvent(e: any) {
    this.afterClick.emit(e);
    this.collection = [];
    this.value = e[this.fieldsShow[0]];
  }

  private selectItem(element: any, index: number) {
    // console.log(element);
    const parent = element.parentElement;
    const list = parent.getElementsByTagName('li');

    for (let li of list) {
      li.className = '';
    }

    if (index >= 0 && index < list.length) {
      let item = list[index];
      item.className = 'active';
    } else {
      this.arrowkeyLocation = 0;
    }

  }
}



