import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { AppService } from '../../services';
import { Router } from '@angular/router';

@Component({
  selector: 'nx-menu',
  styles: [require('./menu.component.scss')],
  template: require('./menu.component.html'),
})
export class MenuComponent implements OnInit {
  @Input() public menuItems: any[];
  @Input() public favsItems: any[];

  @Input() public focus: boolean = false;
  @Input() public activeMenu: boolean = false;
  @Input() public activeSearch: boolean = false;

  @Output()
  public launch: EventEmitter<any> = new EventEmitter<any>();

  @Input() public title: string;

  @Input()
  public nxId = 'nx-sidebar-menu';

  public activeSidebar = true;

  public openSearch(active: boolean) {
    this.activeSearch = active;
    this.activeMenu = false;
    this.focus = active;
    this.launch.emit(active);
  }

  public openMenu(active: boolean) {
    this.launch.emit(false);
    this.activeSearch = false;
    this.activeMenu = active;
    this.focus = active; // there is something wrong here
  }

  public clickEvent(route: string) {
    this.openMenu(false);
    this.router.navigate([route]);
  }

  /**
   * Component constructor with injected dependencies.
   * @param appService
   */
  public constructor(private router: Router) { }

  /**
   * Implements onInit event handler.
   */
  ngOnInit() {}
}



