import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'nx-login-step-two',
  template: require('./nx-login-step-two.component.html'),
  styles: [require('./nx-login-step-two.component.scss')]
})
export class NxLoginStepTwoComponent implements OnInit {
  @ViewChild('userPassword') trgFocusEl: ElementRef;

  @Output() public helpEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output() public recoveryEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output() public nextEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output() public cancelEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output() public geoAuth: EventEmitter<any> = new EventEmitter<any>();

  @Input() public userName: string;


  @Input() public notMeUrl: string;



  @Input() public flag: boolean = true;
  @Input() public lockMessage: any = {
    title:'Importante',
    description: 'La banca empresarial se encuentra fuera de servicio, por favor vuelve a intentarlo más tarde '
  };

  @Input()
  public inputSettings = {
    inputLabel: '',
    inputMinChars: 0,
    inputMaxChars: 0,
    browserVersionsRef: ''
  };

  @Input() public aviso = {
    primero:"Tu Banca Electrónica NUNCA te pedirá:",
    lista1:"-Usuario, contraseña o token por correo o vía telefónica.",
    lista2:"-Información personal o confidencial por ningún medio.",
    lista3:"-Instalar software o descargar aplicaciones/archivos en tu computadora.",
		ultimo:"Mantén actualizado tu antivirus y consulta los consejos básicos de seguridad en nuestro portal en la sección Conoce más > Seguridad Digital."
  }
  @Input() public muestraAviso = true;

  @Input() public errorMsg = '';
  @Output() public errorMsgChange: EventEmitter<string> = new EventEmitter<
    string
  >();

  @Input()
  public logoUrl = '/assets/img/logo.png';

  @Input() public geoCheckAuth: boolean = false;

  @Input()
  public geoLocation: any = {
    banca: 'Banregio',
    pdf1: '../../../../assets/test.pdf',
    pdf2: '../../../../assets/test.pdf',
  }

  @Input()
  public contactInfo = [
    {place: 'Monterrey', phone: '2267 3446', description: '(BANREGIO)'},
    {place: 'Resto del país', phone: '01 81 2267 3446', description: '(BANREGIO)'},
    {place: 'EUA y Canadá', phone: '1 866 4320 550', description: ''}
  ];

  public errorBrowserMsg = '';

  public password: string;
  public disabledBtn = true;
  public geoCheck: boolean = true;

  isHide = true;

  constructor(private router: Router) {
    this.notMeUrl = '/';
  }

  ngOnInit() {
    this.trgFocusEl.nativeElement.focus();
    this.checkBrowsers();
  }

  public helpClick(e: any) {
    this.recoveryEvent.emit(e);
  }

  public nextClick(e: any) {
    this.nextEvent.emit(this.password);
  }

  geoAuthEvent(e:any){
    this.geoAuth.emit(this.geoCheck);
  }

  public cancelClick(e: any) {
    this.cancelEvent.emit(e);
    // this.router.navigate([this.notMeUrl]);
  }

  public keyupEvent(e: any) {
    this.errorMsg = '';
    this.errorMsgChange.emit(this.errorMsg);

    if (this.password.length >= this.inputSettings.inputMinChars) {
      this.disabledBtn = false;

      if (e.keyCode === 13 || e.code === 'Enter') {
        this.nextClick(e);
      }
    } else {
      this.disabledBtn = true;
    }

    if (this.password.length > this.inputSettings.inputMaxChars) {
      this.password = this.password.substr(0, this.inputSettings.inputMaxChars);
    }
  }

  public onPaste(e: any) {
    if (e && e.target) {
      this.password = e.target.value;

      e['keyCode'] = 0;

      this.keyupEvent(e);
    }
  }
  public checkBrowsers() {
    const sayswho = (function() {
      let ua = navigator.userAgent,
        tem,
        M =
          ua.match(
            /(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i
          ) || [];
      if (/trident/i.test(M[1])) {
        tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
        return 'IE ' + (tem[1] || '');
      }
      if (M[1] === 'Chrome') {
        tem = ua.match(/\b(OPR|Edge)\/(\d+)/);
        if (tem != null)
          return tem
            .slice(1)
            .join(' ')
            .replace('OPR', 'Opera');
      }
      M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
      if ((tem = ua.match(/version\/(\d+)/i)) != null) M.splice(1, 1, tem[1]);
      return M.join(' ');
    })();

    if (
      sayswho.indexOf('Opera') > -1 ||
      sayswho.indexOf('IE ') > -1
    ) {
      this.errorBrowserMsg =
      'versiones soportadas.';
    } else {
      let _version = sayswho.length > 4 ? sayswho.split(' ') : ['', '70'];
      // console.log(+_version[1]);
      if (+_version[1] < 45 && _version[0] != 'Safari') {
        this.errorBrowserMsg =
          'versiones soportadas.';
      }
    }
    const browserData = sayswho.length > 4 ? sayswho.split(' ') : ['', '70']
    if(browserData[0] === 'Chrome' && parseInt(browserData[1]) <= 50){
      this.errorBrowserMsg = 'versiones soportadas';
    }
  }

  public showPassword() {
    this.isHide = !this.isHide;
  }
}



