import {
  NgModule,
  Component,
  ElementRef,
  AfterViewInit,
  OnDestroy,
  Input,
  Output,
  AfterViewChecked,
  EventEmitter,
  Renderer2,
  ContentChild,
  NgZone
} from '@angular/core';

import { CommonModule } from '@angular/common';

import { Confirmation } from '../../models/confirm';
import { ConfirmationService } from '../../services/confirm.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'nx-confirm-dialog',
  template: require('./confirm-dialog.component.html')
})
export class ConfirmDialogComponent
  implements AfterViewInit, AfterViewChecked, OnDestroy {
  @Input() question: string;
  @Input() icon: string;
  @Input() header: string = '';
  @Input() message: string;
  @Input() acceptIcon: string = 'fa-check';
  @Input() acceptLabel: string = 'Yes';
  @Input() acceptVisible: boolean = true;
  @Input() rejectIcon: string = 'fa-close';
  @Input() rejectLabel: string = 'No';
  @Input() rejectVisible: boolean = true;
  @Input() acceptButtonStyleClass: string;
  @Input() rejectButtonStyleClass: string;
  @Input() width: any;
  @Input() height: any;
  @Input() closeOnEscape: boolean = true;
  @Input() rtl: boolean;
  @Input() closable: boolean = true;
  @Input() responsive: boolean = true;
  @Input() appendTo: any;
  @Input() key: string;
  @Input() lblOkBtn: string = 'Aceptar';
  @Input() lblCancelBtn: string = 'Cancelar';

  @Input()
  public nxId = 'nx-confirm-dialog';

  confirmation: Confirmation;
  _visible: boolean;
  documentEscapeListener: any;
  documentResponsiveListener: any;
  mask: any;
  contentContainer: any;
  positionInitialized: boolean;
  subscription: Subscription;
  executePostShowActions: boolean;

  constructor(
    public el: ElementRef,
    public renderer: Renderer2,
    private confirmationService: ConfirmationService,
    public zone: NgZone
  ) {
    this.subscription = confirmationService.requireConfirmation$.subscribe(
      confirmation => {
        if (confirmation.close) {
          this.visible = false;
          this.reject();
        } else if (confirmation.key === this.key) {
          this.confirmation = confirmation;
          this.question = this.confirmation.message || this.question;
          this.lblOkBtn = this.confirmation.lblOkBtn || this.lblOkBtn;
          this.lblCancelBtn =
            this.confirmation.lblCancelBtn || this.lblCancelBtn;
          this.icon = this.confirmation.icon || this.icon;
          this.header = this.confirmation.header || this.header;
          this.rejectVisible =
            this.confirmation.rejectVisible == null
              ? this.rejectVisible
              : this.confirmation.rejectVisible;
          this.acceptVisible =
            this.confirmation.acceptVisible == null
              ? this.acceptVisible
              : this.confirmation.acceptVisible;

          if (this.confirmation.accept) {
            this.confirmation.acceptEvent = new EventEmitter();
            this.confirmation.acceptEvent.subscribe(this.confirmation.accept);
          }

          if (this.confirmation.reject) {
            this.confirmation.rejectEvent = new EventEmitter();
            this.confirmation.rejectEvent.subscribe(this.confirmation.reject);
          }

          this.visible = true;
        }
      }
    );
  }

  @Input() get visible(): boolean {
    return this._visible;
  }

  set visible(val: boolean) {
    this._visible = val;

    if (this._visible) {
      if (!this.positionInitialized) {
        this.center();
        this.positionInitialized = true;
      }

      this.executePostShowActions = true;
    }

    if (this._visible) {
      this.enableModality();
    } else {
      this.disableModality();
    }
  }

  ngAfterViewInit() {
    //
  }

  ngAfterViewChecked() {
    //
  }

  center() {
    //
  }

  enableModality() {
    //
  }

  disableModality() {
    //
  }

  close(event: Event) {
    if (this.confirmation.rejectEvent) {
      this.confirmation.rejectEvent.emit();
    }

    this.hide();
    event.preventDefault();
  }

  hide() {
    this.visible = false;
  }

  bindGlobalListeners() {
    //
  }

  unbindGlobalListeners() {
    //
  }

  ngOnDestroy() {
    this.disableModality();

    if (this.documentResponsiveListener) {
      this.documentResponsiveListener();
    }

    if (this.documentEscapeListener) {
      this.documentEscapeListener();
    }

    if (this.appendTo && this.appendTo === 'body') {
      document.body.removeChild(this.el.nativeElement);
    }

    this.subscription.unsubscribe();
  }

  accept() {
    if (this.confirmation.acceptEvent) {
      this.confirmation.acceptEvent.emit();
    }

    this.hide();
    this.confirmation = null;
  }

  reject() {
    if (this.confirmation && this.confirmation.rejectEvent) {
      this.confirmation.rejectEvent.emit();
    }

    this.hide();
    this.confirmation = null;
  }
}



