import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { log } from 'util';

@Component({
  selector: 'nx-login-steps',
  template: require('./nx-login-steps.component.html'),
  styles: [require('./nx-login-steps.component.scss')]
})
export class LoginStepsComponent implements OnInit {

  @Output()
  public helpEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public recoveryEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public nextEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public submitEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public cancelEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public redirEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output() public onBlur: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  public step = 1;

  @Input() public restrictionMessage = {
    header: "Seguimos mejorando",
    content : "¡Descarga ya! la App!.",
    imgButton: [{url:"#", img:"../../../../assets/img/appstore.png"}, {url:"#", img:"../../../../assets/img/appstore.png"}],
    footer: "Si deseas realizar ...",
  }

  @Input() public restrictionActive = true;
  @Input () public lockMessage: any = {
    title: 'Se terminaron los intentos',
    paragraph: 'Por tu seguridad y disposiciones oficiales requerimos acceder a tu ubicación. Habilita esta opción desde tu navegador para acceder a tu Banca Electronica.'
  }
  @Input() public geoCheckAuth: boolean = false;
  @Input()
  public logoUrl = '/assets/img/logo.png';

  @Input() public geoFlag: any = true;

  @Input()
  public userName = '';

  @Input()
  public settings = {
    stepOne: {
      inputLabel: 'Escribe tu usuario',
      inputMinChars: 8,
      inputMaxChars: 10,
      browserVersionsRef: ''
    },
    stepTwo: {
      inputLabel: 'Escribe tu password',
      inputMinChars: 8,
      inputMaxChars: 10,
      browserVersionsRef: ''
    },
    stepThree: {
      inputLabel: 'Escribe tu token',
      inputMinChars: 6,
      inputMaxChars: 6
    }
  };

  @Input()
  public geoLocation: any = {
    banca: 'Banregio',
    pdf1: '../../../../assets/test.pdf',
    pdf2: '../../../../assets/test.pdf',
  };

  @Input()
  public errorMessage = '';

  @Output()
  public errorMessageChange: EventEmitter<string> = new EventEmitter<string>();

  @Output()
  public geoAuthorization:  EventEmitter<any> = new EventEmitter<any>();

  @Input()
  public nxId = 'nx-login-steps';

  @Input() public aviso = {
    primero:"Tu Banca Electrónica NUNCA te pedirá:",
    lista1:"-Usuario, contraseña o token por correo o vía telefónica.",
    lista2:"-Información personal o confidencial por ningún medio.",
    lista3:"-Instalar software o descargar aplicaciones/archivos en tu computadora.",
		ultimo:"Mantén actualizado tu antivirus y consulta los consejos básicos de seguridad en nuestro portal en la sección Conoce más > Seguridad Digital."
  }
  @Input() public muestraAviso = true;

  @Input()
  public notMeUrl = '/';

  @Input()
  public contactInfo = [
    {place: 'Monterrey', phone: '2267 3446', description: '(BANREGIO)'},
    {place: 'Resto del país', phone: '01 81 2267 3446', description: '(BANREGIO)'},
    {place: 'EUA y Canadá', phone: '1 866 4320 550', description: ''}
  ];

  @Input() public flag: boolean = true;
  
  @Input() public lockMessageGeo: any = {
    title:'Importante',
    description: 'La banca empresarial se encuentra fuera de servicio, por favor vuelve a intentarlo más tarde '
  };

  constructor(private router: Router) { }

  ngOnInit() {}

  public onBlurEvent( e: any ) {
      this.onBlur.emit(e);
  }

  public helpFn(e: any) {
    this.helpEvent.emit(e);
  }

  public recoveryFn(e: any) {
    this.recoveryEvent.emit(e);
    this.changeMessage();
  }

  public nextFn(e: any) {
    this.nextEvent.emit(e);
    this.changeMessage();
  }

  public geoAuthFn(e: any){
    this.geoAuthorization.emit(e);
  }

  public loginFn(e: any) {
    this.submitEvent.emit(e);
    this.changeMessage();
  }

  public cancelFn(e: any) {
    this.cancelEvent.emit(e);
    this.changeMessage();
  }

  redirectEvent(e:any){
    this.redirEvent.emit(e);
  }

  public changeMessage() {
    this.errorMessage = '';
    this.errorMessageChange.emit(this.errorMessage);
  }

}



