import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'nx-sidenav',
  template: require('./sidenav.component.html')
})
export class SidenavComponent implements OnInit {
  @Input() collection: any[];
  @Input() title: any;

  @Input() logoUrl: string;
  @Input() buttonCaption: string;
  @Input() logoBannerUrl: string;

  @Output()
  public helpEvent: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  public nxId = 'nx-sidenav';

  isSelected = false;

  constructor() { }

  ngOnInit() {}

  public toggleList(event: any) {
    const target = event.target || event.srcElement || event.currentTarget;
    const nestedList = target.parentElement;
    const listItems = nestedList.parentElement.childNodes;

    for (let element of listItems) {
      if (element.localName === 'li' && element.innerText !== nestedList.innerText) {
        element.classList.remove('selected');

        for (let ul of element.childNodes) {
          if (ul.localName === 'ul') {
            ul.classList.remove('oss-list-active');
          }
        }
      }
    }

    // toggle nested list
    if (nestedList.classList.contains('selected')) {
      nestedList.classList.remove('selected');
    } else {
      nestedList.classList.add('selected');
    }

    if (nestedList.children.length > 1) {
      const ul = nestedList.children[2];
      // console.log(ul);

      // toggle nested list
      if (ul.classList.contains('oss-list-active')) {
        ul.classList.remove('oss-list-active');
      } else {
        ul.classList.add('oss-list-active');
      }
    }
  }

  public clickButton(e: any) {
    this.helpEvent.emit(e);
  }

}



