import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter,
  Input
} from '@angular/core';

@Component({
  selector: 'nx-select-editable',
  template: require('./select-editable.component.html')
})
export class SelectEditableComponent implements OnInit {
  @ViewChild('boxy')
  trgFocusEl: ElementRef;

  @Output()
  public saveEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public changeEvent: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  public collection: { value: any; description: any; note: any }[];

  @Input()
  public placeholder: string;

  @Input()
  public setValue = { value: 0, description: '', note: '' };

  @Input()
  public nxId = 'nx-select-editable';

  public editing = false;

  note: string;

  constructor() {}

  ngOnInit() {
    if (this.collection) {
      if (this.setValue.value === 0) {
        this.setValue = this.collection[0];
        this.note = this.setValue.note;
      } else {
        this.collection.forEach((c: any) => {
          if (c.value === this.setValue.value) {
            this.setValue = c;
            this.note = this.setValue.note;
          }
        });
      }
    }
  }

  public editEvent() {
    this.editing = true;
    setTimeout(() => {
      this.trgFocusEl.nativeElement.focus();
    }, 30);
  }

  public cancelEvent() {
    this.editing = false;
  }

  public keyupEvent(e: any) {
    if (e.keyCode === 13) {
      const val = {
        value: this.setValue.value,
        description: this.setValue.description,
        note: this.setValue.note,
        newAlias: this.trgFocusEl.nativeElement.value
      };

      this.saveEvent.emit(val);

      this.collection.forEach(item => {
        if (item.value === this.setValue.value) {
          item.description = val.newAlias;
          this.setValue = item;
        }
      });

      // this.value = data[this.arrowkeyLocation][this.fieldsShow[0]];
      this.trgFocusEl.nativeElement.value = '';
      this.editing = false;
    }
  }

  public onChangeEvent() {
    this.note = this.setValue.note;

    this.changeEvent.emit(this.setValue);
  }

  public compareFn(x: any, y: any): boolean {
    // return x && y ? x.id === y.id : x === y;
    return x && y ? x.value === y.value : x === y;
  }

  public setNote(note: string) {
    this.note = note;
  }
}



