import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from '@angular/core';

@Component({
  selector: 'nx-login-step-three',
  template: require('./nx-login-step-three.component.html'),
  styles: [require('./nx-login-step-three.component.scss')]
})
export class NxLoginStepThreeComponent implements OnInit {
  @ViewChild('userToken') trgFocusEl: ElementRef;

  @Output() public cancelEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output() public nextEvent: EventEmitter<any> = new EventEmitter<any>();
  @Input() public geoFlag: any = false;

  @Input() public userName: string;

  @Input () public lockMessage: any = {
    title: 'Se terminaron los intentos',
    paragraph: 'Por tu seguridad y disposiciones oficiales requerimos acceder a tu ubicación. Habilita esta opción desde tu navegador para acceder a tu Banca Electronica.'
  }
  @Input()
  public inputSettings = {
    inputLabel: '',
    inputMinChars: 0,
    inputMaxChars: 0
  };

  @Input() public errorMsg = '';
  @Output()
  public errorMsgChange: EventEmitter<string> = new EventEmitter<string>();

  @Input()
  public logoUrl = '/assets/img/logo.png';

  @Input()
  public contactInfo = [
    {place: 'Monterrey', phone: '2267 3446', description: '(BANREGIO)'},
    {place: 'Resto del país', phone: '01 81 2267 3446', description: '(BANREGIO)'},
    {place: 'EUA y Canadá', phone: '1 866 4320 550', description: ''}
  ];

  public token = '';
  public disabledBtn = true;

  public unamePattern = '^[a-z0-9_-]{8,15}$';

  constructor() {}

  ngOnInit() {
    this.trgFocusEl.nativeElement.focus();
  }

  public cancelClick(e: any) {
    this.cancelEvent.emit(e);
  }

  public nextClick(e: any) {
    this.nextEvent.emit(this.token);
  }

  public keypressEvent(evento: any) {
    const e = evento as KeyboardEvent;

    const validkeys = ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0'];

    if (validkeys.indexOf(e.key) >= 0 || (e.keyCode === 13 || e.code === 'Enter')) {
      if ((e.keyCode === 13 || e.code === 'Enter') && this.disabledBtn === false) {
        this.nextClick(e);
      }
    } else {
      e.preventDefault();
    }
  }

  public keyupEvent(e: any) {
    // const e = evento as KeyboardEvent;
    this.errorMsg = '';
    this.errorMsgChange.emit(this.errorMsg);

    if (this.token.length >= this.inputSettings.inputMinChars) {
      this.disabledBtn = false;

      if (e.keyCode === 13 || e.code === 'Enter') {
        this.nextClick(e);
      }
    } else {
      this.disabledBtn = true;
    }

    if (this.token.length > this.inputSettings.inputMaxChars) {
      this.token = this.token.substr(0, this.inputSettings.inputMaxChars);
    }
  }

  public onPaste(e: any) {
    if (e && e.target) {
      this.token = e.target.value;

      e['keyCode'] = 0;

      this.keyupEvent(e);
    }
  }
}



