import { Component, OnInit, Input, Output } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'nx-sub-menu-content',
  template: require('./sub-menu-content.component.html'),
  styles: [require('./sub-menu-content.component.scss')]
})
export class SubMenuContentComponent implements OnInit {
  @Input()
  public collection: any[];
  @Input()
  public title: any;
  @Input()
  public progress: any;

  @Input()
  public nxId = 'nx-sub-menu-content';

  private isSelected = false;

  constructor(private router: Router) {}

  ngOnInit() {}

  private toggleList(event: any) {
    const target = event.target || event.srcElement || event.currentTarget;
    const nestedList = target.parentElement;
    const listItems = nestedList.parentElement.childNodes;

    for (let element of listItems) {
      if (element.localName === 'li' && element.innerText !== nestedList.innerText) {
        element.classList.remove('selected');

        for (let ul of element.childNodes) {
          if (ul.localName === 'ul') {
            ul.classList.remove('oss-list-active');
          }
        }
      }
    }

    // toggle nested list
    if (nestedList.classList.contains('selected')) {
      nestedList.classList.remove('selected');
    } else {
      nestedList.classList.add('selected');
    }

    if (nestedList.children.length > 1) {

      const ul = nestedList.children[1];
      // console.log(ul);

      // toggle nested list
      if (ul.classList.contains('oss-list-active')) {
        ul.classList.remove('oss-list-active');
      } else {
        ul.classList.add('oss-list-active');
      }
    }
  }

}



