import { Component, SimpleChange, OnChanges, Input } from "@angular/core";
import { Chart } from "chart.js";

import { deepExtend } from "./helper";

@Component({
  selector: "nx-line-chart",
  template: require('./line-chart.component.html')
})
export class LineChartComponent implements OnChanges {
  @Input() public settings = {};

  @Input()
  public nxId = 'nx-line-chart';

  public chart: Chart;

  public defaultSettings: object = {
    type: "line",
    data: {
      labels: [],
      datasets: []
    },
    options: {
      elements: {
        point: {
          radius: 5,
          backgroundColor: "#fff",
          borderWidth: 2
        },
        line: {
          borderWidth: 3
        }
      },
      legend: {
        display: false
      },
      scales: {
        xAxes: [
          {
            display: true,
            gridLines: {
              display: false
            }
          }
        ],
        yAxes: [
          {
            display: true,
            gridLines: {
              borderDash: [4, 2]
            }
          }
        ]
      },
      tooltips: {
        backgroundColor: "#fff",
        titleFontColor: "#000",
        bodyFontColor: "#27CD6E",
        displayColors: false,
        borderColor: "#d8d8d8",
        borderWidth: 1
      }
    }
  };

  constructor() {}

  ngOnChanges(changes: { [propertyName: string]: SimpleChange }) {
    const params = this.prepareSettings();
    console.log(params);

    if (this.settings) {
      this.setChart(params);
    }
  }

  public setChart(settings: any) {
    this.chart = new Chart("line-chart-canvas", settings);
  }

  private prepareSettings(): object {
    return deepExtend({}, this.defaultSettings, this.settings);
  }
}



