import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'nx-table-row-detail-b',
  template: require('./table-row-detail-b.component.html')
})
export class NxTableRowDetailBComponent implements OnInit {
  @Input()
  public content: any;

  @Input()
  public reference: any;

  @Output()
  public showEvent: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  public downloadEvent: EventEmitter<any> = new EventEmitter<any>();
  @Output()
  public printEvent: EventEmitter<any> = new EventEmitter<any>();

  public detailKeys: any = [];

  constructor() {}

  ngOnInit() {
    // console.log(this.content);

    // if (this.content.detail) {
    //   Object.keys(this.content.detail).forEach((key: any) => {
    //     this.detailKeys.push(key);
    //   });
    // }
  }

  public showClick(_event: any) {
    this.showEvent.emit(_event);
  }

  public isValidContent(e: any) {
    const rtn = e.detail && Array.isArray(e.detail) ? true : false;
    // console.log(rtn);

    return rtn;
  }

  public printClick(_event: any) {
    this.printEvent.emit(_event);
  }
  public downloadClick(_event: any) {
    this.downloadEvent.emit(_event);
  }
}



