import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter,
  Input
} from '@angular/core';

@Component({
  selector: 'nx-title-editable',
  template: require('./title-editable.component.html')
})
export class TitleEditableComponent implements OnInit {
  @ViewChild('boxy')
  trgFocusEl: ElementRef;

  @Output()
  public saveEvent: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  public title: any;

  @Output()
  public titleChange = new EventEmitter();

  @Input()
  public placeholder: string;

  @Input()
  public nxId = 'nx-title-editable';

  @Input()
  public maxLengthInput: number = 100;

  public editing = false;

  public description: string;

  public note: string;

  constructor() {}

  ngOnInit() {}

  public editEvent() {
    this.editing = true;
    setTimeout(() => {
      this.trgFocusEl.nativeElement.focus();
    }, 30);
  }

  public cancelEvent() {
    this.editing = false;
  }

  public keyupEvent(e: any) {
    if (e.keyCode === 13) {
      const val = {
        description: this.title.description,
        note: this.title.note,
        newAlias: this.trgFocusEl.nativeElement.value
      };

      this.saveEvent.emit(val);
      this.titleChange.emit({ description: val.newAlias, note: val.note });

      // this.title.description = val.newAlias;
      // this.description = this.title.description;
      // this.note = this.title.note;

      // this.value = data[this.arrowkeyLocation][this.fieldsShow[0]];
      this.trgFocusEl.nativeElement.value = '';
      this.editing = false;
    }
  }

  public compareFn(x: any, y: any): boolean {
    // return x && y ? x.id === y.id : x === y;
    return x && y ? x.value === y.value : x === y;
  }

  public setNote(note: string) {
    this.note = note;
  }
}



