import { Component, OnInit, Input, Output, EventEmitter,
  OnChanges, SimpleChanges } from '@angular/core';
import { forEach } from '@angular/router/src/utils/collection';
import { Router } from '@angular/router';

import { Item } from '../../models/kanban-item';
import { Header } from '../../models/kanban-header';

@Component({
  selector: 'nx-kanban',
  template: require('./kanban.component.html'),
  styles: [require('./kanban.component.scss')]
})
export class KanbanComponent implements OnInit {
  @Input() collection: Item[];
  @Input() columns: Header[];
  @Input() launcher_label: string;
  @Input() route: string;
  @Input() hideLauncher: boolean = false;

  @Output()
  launch: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  afterClick: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  public nxId = 'nx-kanban';

  maxHeight: any;

  data: Header[];

  itemCounter: number = 0;

  constructor(private router: Router) {}

  ngOnInit() {
    // this.data = this.groupBy(this.columns, this.collection);
    const kanbanArray = document.getElementsByClassName('kanban') as HTMLCollectionOf<HTMLElement>;
    const kanban = kanbanArray[0];
    this.maxHeight = kanban.offsetTop;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['collection']) {
      this.data = this.groupBy(this.columns, this.collection);
    }
  }

  public setCollection(_data: Item[]) {
    // this.data = this.groupBy(this.columns, _data);
  }

  private groupBy(_h: Header[], _d: Item[]) {
    _h.forEach(column => {
      _d.forEach(item => {
        if (column.key === item.type) {
          column.data.push(item);
        }
      });
    });
    return _h;
  }

  private launchEvent(e: any) {
    this.launch.emit(e);
  }

  private cardEvent(e: any, i: any) {
    this.afterClick.emit(i);
  }

  private goForward(id: any) {
    this.router.navigate([this.route + '/' + id]);
  }

  private showLauncher(index: number) {
    if (index === 0 && this.hideLauncher === false) {
      return true;
    } else {
      return false;
    }
  }

}



